import React, { useState } from "react";
import useStore from "../../store"; // Zustand store import
import LogoutModal from "../LogoutModal";

const UserCard = () => {
  const { User, setUser } = useStore();
  const [InputVaule, setInputVaule] = useState("");
  const [isModalOpen, setIsModalOpen] = useState(false); //   

  const Submit = (event) => {
    event.preventDefault();
    setUser(InputVaule); // User  
  };

  const InputChange = (event) => {
    setInputVaule(event.target.value);
  };

  const openModal = () => {
    setIsModalOpen(true); //  
  };

  const closeModal = () => {
    setIsModalOpen(false); //  
  };

  const handleLogout = () => {
    localStorage.clear();
    setUser("Guest"); //   User  "Guest" 
    closeModal(); //  
  };
  return (
    <div className="userCard">
      <form onSubmit={Submit}>
        {User === "Guest" ? (
          <input
            type="text"
            placeholder="YOUR NAME"
            size="9"
            value={InputVaule}
            onChange={InputChange}
          />
        ) : null}
        <div className="line-h25">
          {User === "Guest" ? (
            <>
              <p className="f-b f-s white">{User}, .</p>
              <p className="f-s white f-c"> </p>
            </>
          ) : (
            <div className="m-x">
              <span className="f-b f-s white flex">
                <p className="text-over">{User}</p>
                <p>,</p>
              </span>
              <p className="f-b f-s white">.</p>
              <button className="button" type="button" onClick={openModal}>
                
              </button>
              <LogoutModal
                isOpen={isModalOpen}
                onClose={closeModal}
                onLogout={handleLogout}
              />
            </div>
          )}
        </div>
      </form>
    </div>
  );
};

export default UserCard;
